/* 重置預設樣式 */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  background-color: #F5F5F5;
  color: #333;
  line-height: 1.5;
}

/* ===== 主要內容與頁尾 ===== */
main {
  padding: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

footer {
  text-align: center;
  padding: 1rem;
  background-color: #fff;
  border-top: 1px solid #E0E0E0;
  margin-top: 2rem;
}

/* 關於我們頁面樣式 */
.about-section {
  margin-bottom: 2rem;
}

.about-section h2 {
  color: #000000;
  margin-bottom: 1rem;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid #e0e0e0;
}

.about-section p {
  margin-bottom: 1rem;
  line-height: 1.8;
}

/* 共享元素樣式 */
.page-title {
  font-size: 2rem;
  color: #171a20;
  margin-bottom: 1.5rem;
  padding-bottom: 0.5rem;
  border-bottom: 2px solid #3e6ae1;
  display: inline-block;
}

:root {
  --primary-color: #CC0000; /* 更改主色調為紅色，原本是 #6a4c93 */
  --primary-hover: #AA0000; /* 對應的懸停色，較深一點 */
  --accent-color: #FFCC00; /* 搭配的輔助色，金黃色 */
  --background-color: #fff;
  --text-color: #333;
}

a {
  color: var(--primary-color);
  text-decoration: none;
  transition: color 0.3s;
}

a:hover {
  color: var(--primary-hover);
} 