.news-container {
  max-width: 900px;
  margin: 40px auto;
  padding: 0 20px;
}

.news-header {
  text-align: center;
  margin-bottom: 40px;
}

.news-header h1 {
  font-size: 2.2rem;
  color: #333;
  margin-bottom: 15px;
}

.news-header p {
  color: #666;
  font-size: 1.1rem;
}

.news-list {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.news-item {
  display: flex;
  background-color: #fff;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 3px 12px rgba(0, 0, 0, 0.08);
  transition: transform 0.3s ease;
}

.news-item:hover {
  transform: translateY(-5px);
}

.news-date {
  min-width: 120px;
  background-color: #CC0000;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  font-weight: 600;
  text-align: center;
  font-size: 0.9rem;
}

.news-content {
  padding: 25px;
  flex: 1;
}

.news-title {
  font-size: 1.4rem;
  color: #333;
  margin-bottom: 15px;
}

.news-summary {
  color: #555;
  line-height: 1.6;
  margin-bottom: 15px;
}

.news-details {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease;
}

.news-full-content {
  color: #555;
  line-height: 1.7;
  padding-top: 15px;
  border-top: 1px solid #eee;
}

.news-read-more {
  background: none;
  border: none;
  color: #CC0000;
  cursor: pointer;
  font-size: 0.95rem;
  font-weight: 500;
  padding: 5px 0;
  display: flex;
  align-items: center;
  gap: 5px;
}

.arrow-icon {
  transition: transform 0.3s ease;
}

.news-read-more:hover .arrow-icon {
  transform: translateY(3px);
}

/* 當點擊展開時的樣式 (可透過JavaScript實現) */
.news-item.expanded .news-details {
  max-height: 500px;
}

.news-item.expanded .arrow-icon {
  transform: rotate(180deg);
}

/* 響應式設計 */
@media (max-width: 768px) {
  .news-item {
    flex-direction: column;
  }
  
  .news-date {
    width: 100%;
    padding: 10px;
  }
  
  .news-header h1 {
    font-size: 1.8rem;
  }
  
  .news-title {
    font-size: 1.2rem;
  }
} 