/* 首頁樣式 */
.home-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 20px;
}

/* 動畫歡迎區 */
.hero-section {
  text-align: center;
  margin-bottom: 60px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.95);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 999;
  animation: fadeIn 1s ease-in-out;
}

.hero-section.animated {
  animation: fadeIn 1s ease-in-out, fadeOut 1s ease-in-out 4s;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fadeOut {
  from { opacity: 1; }
  to { opacity: 0; }
}

.hero-section h1 {
  font-size: 2.8rem;
  color: #333;
  margin-bottom: 20px;
  animation: slideUp 0.8s ease-out 0.2s both;
}

.hero-section p {
  font-size: 1.2rem;
  color: #666;
  max-width: 800px;
  margin: 0 auto 30px;
  line-height: 1.8;
  animation: slideUp 0.8s ease-out 0.5s both;
}

@keyframes slideUp {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.features-section {
  margin-bottom: 60px;
}

.features-section h2 {
  font-size: 2.2rem;
  color: #333;
  margin-bottom: 30px;
  text-align: center;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
}

.feature-card {
  background-color: #fff;
  border-radius: 8px;
  padding: 30px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s, box-shadow 0.3s;
}

.feature-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
}

.feature-card h3 {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 15px;
}

.feature-card p {
  color: #666;
  line-height: 1.6;
}

.product-section {
  margin-bottom: 60px;
}

.product-section h2 {
  font-size: 2.2rem;
  color: #333;
  margin-bottom: 30px;
  text-align: center;
}

.product-description {
  text-align: center;
  max-width: 800px;
  margin: 0 auto 40px;
  color: #666;
  line-height: 1.8;
  font-size: 1.1rem;
}

/* 新的產品展示 */
.product-showcase {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}

.product-category {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #fff;
  border-radius: 8px;
  padding: 30px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s, box-shadow 0.3s;
  margin-bottom: 20px;
}

.product-category:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
}

.product-category h3 {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 15px;
  text-align: center;
}

.product-preview {
  flex-grow: 1;
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.product-preview-item {
  flex: 0 0 48%;
  margin-bottom: 15px;
  transition: transform 0.3s ease;
}

.product-preview-item:hover {
  transform: translateY(-5px);
}

.product-preview-item img, 
.product-preview-image {
  width: 100%;
  height: 180px;
  object-fit: contain;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  background-color: #fafafa;
  transition: transform 0.3s ease;
}

/* 註解掉 hover 效果
.product-preview-item:hover img,
.product-preview-item:hover .lazy-image {
  transform: scale(1.05);
}
*/

.product-preview-item p {
  text-align: center;
  color: #333;
  font-size: 0.9rem;
  margin: 0;
}

.view-all-link {
  display: block;
  text-align: center;
  color: var(--primary-color);
  text-decoration: none;
  font-weight: bold;
  margin-top: 15px;
  transition: color 0.3s;
  font-size: 1.1rem;
}

.view-all-link:hover {
  color: var(--primary-hover);
  text-decoration: underline;
}

.cta-section {
  display: none;
}

@media (max-width: 992px) {
  .features-grid, .product-showcase {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .hero-section h1 {
    font-size: 2.2rem;
  }
  
  .features-grid, .product-showcase {
    grid-template-columns: 1fr;
  }
  
  .cta-section {
    padding: 40px 20px;
  }
} 