/* Footer Styles */
.footer {
  background-color: #333;
  color: #fff;
  padding: 30px 0;
  font-size: 0.9rem;
}

.footer-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer-content {
  text-align: center;
  margin-bottom: 20px;
}

.footer-content p {
  margin: 5px 0;
}

.footer-content .company-name {
  font-weight: bold;
  font-size: 1.2rem;
  margin-bottom: 10px;
}

.footer-bottom {
  width: 100%;
  text-align: center;
  padding-top: 20px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.footer-bottom p {
  margin: 5px 0;
  color: #aaa;
}

@media (max-width: 768px) {
  .footer {
    padding: 20px 0;
  }
} 