.product-detail-container {
  max-width: 1200px;
  margin: 30px auto;
  padding: 0 20px;
}

/* 載入中與錯誤提示 */
.product-detail-container.loading,
.product-detail-container.error,
.product-detail-container.not-found {
  padding: 50px 20px;
  text-align: center;
}

/* 麵包屑導航 */
.breadcrumbs {
  margin-bottom: 20px;
  font-size: 1rem;
  color: #666;
}

.breadcrumbs a {
  text-decoration: none;
  color: var(--primary-color);
}

.breadcrumbs a:hover {
  text-decoration: underline;
}

/* 產品詳情主內容 */
.product-detail-content {
  display: flex;
  gap: 40px;
  margin-bottom: 50px;
}

/* 左側產品圖片 */
.product-detail-left {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.product-image-container {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 75%; /* 適合展示各種工具的寬高比 */
  background-color: #f8f8f8;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 3px 12px rgba(0, 0, 0, 0.08);
  cursor: pointer;
  transition: all 0.3s ease;
  margin-bottom: 25px;
}

.product-image-container:hover {
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.12);
  transform: translateY(-3px);
}

.product-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.product-image img {
  max-width: 85%;
  max-height: 85%;
  object-fit: contain;
  transition: transform 0.3s ease;
}

.product-image-container:hover img {
  transform: scale(1.03);
}

