.product-image-container:hover .zoom-hint {
  opacity: 1;
}

.zoom-icon {
  margin-right: 5px;
}

/* 放大圖片蓋層 */
.zoomed-image-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.85); /* 加深背景色以突出產品圖片 */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease-in-out;
  backdrop-filter: blur(3px); /* 添加背景模糊效果 */
}

.zoomed-image-container {
  width: 90%;
  max-width: 1000px; /* 增加最大寬度 */
  height: 80vh;
  max-height: 800px; /* 增加最大高度 */
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.zoomed-image {
  max-width: 95%;
  max-height: 95%;
  object-fit: contain;
  box-shadow: 0 5px 25px rgba(0, 0, 0, 0.2); /* 添加陰影使圖片突出 */
  border-radius: 4px;
  transition: transform 0.3s ease;
}

.close-zoom {
  position: absolute;
  top: -40px;
  right: 0;
  color: white;
  font-size: 32px;
  cursor: pointer;
  background: none;
  border: none;
  padding: 10px;
  opacity: 0.8;
  transition: opacity 0.3s ease, transform 0.3s ease;
}

.close-zoom:hover {
  opacity: 1;
  transform: scale(1.1); /* 添加輕微縮放效果 */
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.zoomed-image-overlay {
  animation: fadeIn 0.3s ease-in-out;
}

/* 右側產品資訊 */
.product-detail-right {
  flex: 1;
}

.product-title {
  font-size: 2.2rem;
  margin-bottom: 15px;
  color: #333;
}

.product-info {
  margin-bottom: 30px;
}

.product-description {
  margin-bottom: 25px;
  line-height: 1.7;
  color: #444;
  font-size: 1.1rem;
}

.product-specs, .product-features {
  margin-bottom: 20px;
}

.product-specs h3, .product-features h3 {
  color: #333;
  font-size: 1.4rem;
  margin-bottom: 15px;
}

.product-specs ul, .product-features ul {
  padding-left: 20px;
}

.product-specs li, .product-features li {
  margin-bottom: 10px;
  color: #444;
  font-size: 1.1rem;
}

/* 按鈕區域 */
.product-actions {
  display: flex;
  gap: 15px;
}

.btn-order, .btn-inquire {
  padding: 12px 25px;
  font-size: 1.1rem;
  border-radius: 5px;
  font-weight: 500;
  transition: all 0.3s ease;
  cursor: pointer;
  margin-right: 15px;
  text-decoration: none;
}

.btn-order {
  background-color: var(--primary-color);
  color: white;
  border: none;
}

.btn-order:hover {
  background-color: var(--primary-hover);
  color: white;
}

.btn-inquire {
  color: var(--primary-color);
  border: 1px solid var(--primary-color);
  background-color: transparent;
}

.btn-inquire:hover {
  background-color: rgba(204, 0, 0, 0.1); /* 淡紅色背景 */
}

.btn-back {
  display: inline-block;
  margin-top: 20px;
  padding: 12px 25px;
  background-color: var(--primary-color);
  color: white;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s ease;
  font-size: 1.1rem;
}

.btn-back:hover {
  background-color: var(--primary-hover);
}

/* 相關產品區域 */
.related-products {
  margin-top: 50px;
}

.related-products h2 {
  font-size: 2rem;
  margin-bottom: 20px;
  color: #333;
  position: relative;
  padding-bottom: 10px;
}

.related-products h2::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 60px;
  height: 3px;
  background-color: var(--primary-color); /* 添加紅色下劃線 */
}

.related-products-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
}

.related-product {
  background-color: #fff;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  overflow: hidden;
  transition: transform 0.3s, box-shadow 0.3s;
  margin-bottom: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

.related-product:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
}

.related-product a {
  text-decoration: none;
  color: #333;
  display: block;
}

.related-product-image {
  background-color: #f8f8f8;
  padding: 15px;
  height: 210px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px 8px 0 0;
  overflow: hidden;
  transition: background-color 0.3s ease;
}

.related-product-image img {
  max-width: 85%; /* 調整圖片大小 */
  max-height: 85%; /* 調整圖片大小 */
  object-fit: contain;
  transition: transform 0.3s ease;
}

.related-product:hover .related-product-image {
  background-color: #f0f0f0;
}


.related-product-name {
  padding: 15px;
  font-weight: 500;
  text-align: center;
  color: #333;
  font-size: 1.1rem;
}

/* 響應式設計 */
@media (max-width: 992px) {
  .product-image-container {
    padding-bottom: 70%; /* 在中等屏幕上減少高度 */
  }
  
  .related-products-grid {
    grid-template-columns: repeat(3, 1fr);
    gap: 15px;
  }
}

@media (max-width: 768px) {
  .product-detail-content {
    flex-direction: column;
  }
  
  .product-image-container {
    padding-bottom: 65%; /* 在小屏幕上進一步減少高度 */
    margin-bottom: 20px;
  }
  
  .related-products-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 12px;
  }
  
  .related-product-image {
    height: 180px; /* 在平板上減少高度 */
  }
}

@media (max-width: 480px) {
  .product-image-container {
    padding-bottom: 80%; /* 在手機上增加高度比例 */
  }
  
  .related-products-grid {
    grid-template-columns: 1fr;
  }
  
  .related-product-image {
    height: 220px; /* 在手機上增加高度，因為寬度減小 */
  }
  
  .product-actions {
    flex-direction: column;
  }
}

.product-detail-container.loading {
  min-height: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 50px 0;
}

.loading-animation {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80px;
  height: 80px;
  margin-bottom: 20px;
}

.loading-spinner {
  width: 50px;
  height: 50px;
  border: 3px solid rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  border-top-color: #3e6ae1;
  animation: spin 1s linear infinite;
}

.loading-text {
  color: #666;
  font-size: 1.1rem;
}

.product-detail-container.error,
.product-detail-container.not-found {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 100px 0;
  text-align: center;
}

.error-icon,
.not-found-icon {
  font-size: 48px;
  color: #e74c3c;
  margin-bottom: 20px;
}

.not-found-icon {
  color: #3498db;
}

.error-text {
  max-width: 500px;
  margin-bottom: 30px;
}

.error-text h2 {
  color: #e74c3c;
  margin-bottom: 10px;
  font-size: 1.8rem;
}

.not-found-text h2 {
  color: #3498db;
  margin-bottom: 10px;
  font-size: 1.8rem;
}

.error-text p,
.not-found-text p {
  color: #666;
  margin-bottom: 20px;
} 