/* 全局樣式設定 */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Arial', '微軟正黑體', sans-serif;
  line-height: 1.6;
  color: #333;
  background-color: #f8f9fa;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

/* 主要內容區域 */
.main-content {
  min-height: calc(100vh - 160px); /* 假設導航欄和頁腳高度總共為 160px */
  padding: 30px 0;
}

/* 標題樣式 */
h1, h2, h3 {
  margin-bottom: 20px;
  color: #333;
}

h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.5rem;
}

/* 段落樣式 */
p {
  margin-bottom: 15px;
  line-height: 1.8;
}

/* 按鈕樣式 */
button, .btn {
  display: inline-block;
  padding: 8px 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  text-decoration: none;
  font-size: 1rem;
  transition: background-color 0.3s;
}

button:hover, .btn:hover {
  background-color: #0056b3;
}

/* 圖片樣式 */
img {
  max-width: 100%;
  height: auto;
}

/* 響應式設計 */
@media (max-width: 768px) {
  h1 {
    font-size: 2rem;
  }
  
  h2 {
    font-size: 1.8rem;
  }
  
  h3 {
    font-size: 1.3rem;
  }
  
  .container {
    padding: 0 15px;
  }
}
