/* 關於我們頁面樣式 */
.about-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 20px;
}

.about-header {
  text-align: center;
  margin-bottom: 50px;
}

.about-header h1 {
  font-size: 2.8rem;
  color: #333;
  margin-bottom: 20px;
}

.about-header p {
  font-size: 1.2rem;
  color: #666;
  max-width: 800px;
  margin: 0 auto;
  line-height: 1.8;
}

.about-section {
  margin-bottom: 50px;
  padding: 30px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.about-section h2 {
  font-size: 1.8rem;
  color: #333;
  margin-bottom: 20px;
  padding-bottom: 10px;
  border-bottom: 2px solid #f0f0f0;
}

.about-section p {
  color: #444444;
  line-height: 1.8;
  margin-bottom: 15px;
  font-size: 1.3rem;
}

.about-section strong {
  color: #333;
}

.about-image {
  width: 100%;
  height: auto;
  border-radius: 8px;
  margin: 20px 0;
}

.company-values {
  margin-top: 40px;
}

.values-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  margin-top: 30px;
}

.value-card {
  text-align: center;
  padding: 30px;
  background-color: #f9f9f9;
  border-radius: 8px;
  transition: transform 0.3s, box-shadow 0.3s;
}

.value-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
}

.value-card h3 {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 15px;
}

.value-card p {
  color: #666;
}

.team-section {
  margin-top: 60px;
}

.team-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  margin-top: 30px;
}

.team-member {
  text-align: center;
}

.team-member img {
  width: 100%;
  max-width: 200px;
  border-radius: 50%;
  margin-bottom: 15px;
}

.team-member h3 {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 10px;
}

.team-member p {
  color: #666;
  font-style: italic;
}

@media (max-width: 992px) {
  .values-grid, .team-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .about-header h1 {
    font-size: 2.2rem;
  }
  
  .values-grid, .team-grid {
    grid-template-columns: 1fr;
  }
} 