.lazy-image-container {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 0;
  padding-bottom: 80%; /* 適應各種產品比例，設置固定寬高比 */
  background-color: #f5f5f5; /* 更柔和的背景色 */
  border-radius: 6px; /* 稍微增加圓角 */
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05); /* 添加輕微陰影提升層次感 */
}

.lazy-image {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 90%; /* 限制最大寬度 */
  max-height: 90%; /* 限制最大高度 */
  width: auto;
  height: auto;
  object-fit: contain; /* 確保圖片完整顯示 */
  opacity: 0;
  transition: opacity 0.4s ease-in-out, transform 0.3s ease;
}

.lazy-image.loaded {
  opacity: 1;
  min-width: 60%; /* 確保圖片不會太小 */
  min-height: 60%; /* 確保圖片不會太小 */
}

/* 添加鼠標懸停效果 */
.lazy-image-container:hover .lazy-image.loaded {
  transform: translate(-50%, -50%) scale(1.05); /* 輕微放大效果 */
}

/* 加載動畫 */
.loading-spinner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 30px;
  height: 30px;
  border: 3px solid rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  border-top-color: var(--primary-color); /* 使用主題色變量 */
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: translate(-50%, -50%) rotate(0deg); }
  100% { transform: translate(-50%, -50%) rotate(360deg); }
} 